<template>
  <div class="cart">
    <van-nav-bar title="购物车" fixed />
    <div v-if="(cartTotal>0)">
       <!-- 购物车开头 -->
      <div class="cart-title">
      <span class="all">共<i>{{ cartTotal }}</i>件商品</span>
      <span class="edit" @click="edit()">
        <!-- 可以写函数，也可以直接写表达式 -->
        <van-icon name="edit" />
        编辑
      </span>
      </div>

      <!-- 购物车列表 -->
      <div class="cart-list">
      <div class="cart-item" v-for="item in cartList" :key="item.goods_id">
        <!-- vant的组件，圆形的复选框，不是自己写的vue组件-->
        <van-checkbox :value="item.checked" @click="toggleCheck(item.goods_id)"></van-checkbox>
        <div class="show">
          <img :src="item.goods.goods_image" alt="">
        </div>
        <div class="info">
          <span class="tit text-ellipsis-2">{{ item.goods.goods_name }}</span>
          <span class="bottom">
            <div class="price">¥ <span>{{item.goods.goods_price_min}}</span></div>
            <!-- 希望保留原本的默认传参，又希望传入自己的参数 -->
            <countbox :value="item.goods_num" @input="(e)=>{changeCount(item.goods_id,e,item.goods_sku_id)}"></countbox>
          </span>
        </div>
      </div>
      </div>

      <div class="footer-fixed">
      <div  class="all-check">
         <!-- vant的组件，圆形的复选框，不是自己写的vue组件-->
        <van-checkbox  icon-size="18" :value="isAllChecked" @click="toggleAllCheck()"></van-checkbox>
        全选
      </div>

      <div class="all-total">
        <div class="price">
          <span>合计：</span>
          <span>¥ <i class="totalPrice">{{sum}}</i></span>
        </div>
        <!-- disabled能算是类吗 -->
        <div v-if="!isEdit" class="goPay" :class="{disabled:numOfItems===0}" @click="goPay">结算({{ numOfItems }})</div>
        <div v-else class="delete" :class="{disabled:numOfItems===0}" @click="deleteGoods">删除</div> 
      </div>
      </div>
    </div>
    <div class="empty-cart" v-else>
      <img src="@/assets/empty.png" alt="">
      <div class="tips">
      您的购物车是空的, 快去逛逛吧
      </div>
      <div class="btn" @click="$router.push('/')">去逛逛</div>
    </div>
  </div>
</template>

<script>
import countbox from '@/components/Countbox.vue'
import { mapState,mapGetters} from 'vuex';
export default {
  name: 'CartPage',
  components:{countbox},
  created(){
    if(this.loginConfirm()){
      //发送请求，设置购物车列表，然后才可以进行渲染
      this.$store.dispatch('cart/getCartAction')
      //  console.log(this.$store.state.user.info.token)
    }
  },
  // data(){
  // 没有自己的数据捏，全是vuex里的
  //   return {
  //     addCount:1
  //   }
  // },
  
  data(){
    return {
      isEdit:false
    }
  },
  methods:{
    //确认登录状态
    loginConfirm(){
      if(!this.$store.getters.token){
        //如果未登录，弹出提示
        this.$dialog.confirm({
        title: '温馨提示',
        message: '此时需要先登录才能继续操作哦',
        confirmButtonText: '去登录',
        cancelButtonText: '再逛逛'
      }).then(() => {
        //如果点击去登录
        //replace函数的用法
        this.$router.replace({
          path: '/login',
          query: {
            backUrl: this.$route.fullPath
          }
        })
      }) .catch(() => {
        //如果点击取消，什么也不做
      })
      //非promise对象也能调用这两个方法吗？
        return false
      }
      return true
    },
    //修改数据，委托给vuex中的方法
    toggleCheck(goods_id){
      this.$store.commit('cart/toggleCheck',goods_id)
    },
    //点击全选按钮，触发此函数
    toggleAllCheck(){
      this.$store.commit('cart/toggleAllCheck',!this.isAllChecked)
    //   console.log(1)
    },
    //改变商品数量，处理子组件传递过来的事件委托
    changeCount(goodsId, goodsNum, goodsSkuId){
      // console.log(goodsId, goodsNum, goodsSkuId)
      this.$store.dispatch('cart/changeCountAction',{goodsId, goodsNum, goodsSkuId})
    },
    //编辑商品，可以删除商品
    edit(){
      this.isEdit=!this.isEdit
      this.$store.commit('cart/toggleAllCheck',!this.isEdit)
    },
    //删除操作
    deleteGoods(){
      this.$store.dispatch('cart/deleteGoodsAction')
    },
    //订单确认
    goPay(){
      this.$router.push({
        path:'/pay',
        query:{
          mode:'cart',
          cartIds:this.cartIds 
        }
      })
    }
  },
  computed:{
    //获取vuex中的购物车列表
    ...mapState('cart',['cartList']),
    //
    ...mapGetters('cart', ['isAllChecked']),
    //下面三个计算属性由前面的计算属性决定，动态变化的传递？
    //其实可以写在vuex的getters里
    cartTotal(){
      return this.cartList.reduce((sum, cur) => {
          return sum + cur.goods_num
        }, 0)
    },
    numOfItems() {
        //对象数组也可以使用reduce来求某些和
        return this.cartList.reduce((sum, cur) => {
          if (cur.checked) {
            return sum + cur.goods_num
          } else {
            return sum
          }
        }, 0)
      },
    sum() {
      return this.cartList.reduce((sum, cur) => {
        if (cur.checked) {
          return sum + cur.goods_num * cur.goods.goods_price_min
        } else {
          return sum
        }
      }, 0)
    }, 
    cartIds(){
      return this.cartList.filter(item=>item.checked).map(item=>item.id).join(',')
    }
  }
}
</script>

<style lang="less" scoped>
// 主题 padding
.cart {
  padding-top: 46px;
  padding-bottom: 100px;
  background-color: #f5f5f5;
  min-height: 100vh;
  .cart-title {
    height: 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
    font-size: 14px;
    .all {
      i {
        font-style: normal;
        margin: 0 2px;
        color: #fa2209;
        font-size: 16px;
      }
    }
    .edit {
      .van-icon {
        font-size: 18px;
      }
    }
  }

  .cart-item {
    margin: 0 10px 10px 10px;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    background-color: #ffffff;
    border-radius: 5px;

    .show img {
      width: 100px;
      height: 100px;
    }
    .info {
      width: 210px;
      padding: 10px 5px;
      font-size: 14px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      .bottom {
        display: flex;
        justify-content: space-between;
        .price {
          display: flex;
          align-items: flex-end;
          color: #fa2209;
          font-size: 12px;
          span {
            font-size: 16px;
          }
        }
        .count-box {
          display: flex;
          width: 110px;
          .add,
          .minus {
            width: 30px;
            height: 30px;
            outline: none;
            border: none;
          }
          .inp {
            width: 40px;
            height: 30px;
            outline: none;
            border: none;
            background-color: #efefef;
            text-align: center;
            margin: 0 5px;
          }
        }
      }
    }
  }
}

.footer-fixed {
  position: fixed;
  left: 0;
  bottom: 50px;
  height: 50px;
  width: 100%;
  border-bottom: 1px solid #ccc;
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;

  .all-check {
    display: flex;
    align-items: center;
    .van-checkbox {
      margin-right: 5px;
    }
  }

  .all-total {
    display: flex;
    line-height: 36px;
    .price {
      font-size: 14px;
      margin-right: 10px;
      .totalPrice {
        color: #fa2209;
        font-size: 18px;
        font-style: normal;
      }
    }

    .goPay, .delete {
      min-width: 100px;
      height: 36px;
      line-height: 36px;
      text-align: center;
      background-color: #fa2f21;
      color: #fff;
      border-radius: 18px;
      &.disabled {
        background-color: #ff9779;
      }
    }
  }

}
.empty-cart {
  padding: 80px 30px;
  img {
    width: 140px;
    height: 92px;
    display: block;
    margin: 0 auto;
  }
  .tips {
    text-align: center;
    color: #666;
    margin: 30px;
  }
  .btn {
    width: 110px;
    height: 32px;
    line-height: 32px;
    text-align: center;
    background-color: #fa2c20;
    border-radius: 16px;
    color: #fff;
    display: block;
    margin: 0 auto;
  }
}
</style>