<template>
   <div class="body">
    <van-nav-bar title="添加地址" left-arrow @click-left="onClickLeft"/>
    <form action="">
      <div>地址信息</div>
      <div><label for="">姓名：</label><input type="text" placeholder="请输入姓名" v-model="name"></div>
      <div><label for="">电话：</label><input type="number" placeholder="请输入电话号码" v-model="phone"></div>
      <div><label for="">地址：</label>
        <!-- option触发不了点击事件 -->
        <select name="province" ref="province" @change="changeIndex1" v-if="this.plist.length!==0"><option :value="item.id" v-for="(item,index) in plist" :key="index">{{ item.name }}</option></select>
        <select name="city" ref="city" @change="changeIndex2" v-if="this.plist.length!==0"><option :value="item.id" v-for="(item,index) in  clist" :key="index">{{ item.name }}</option></select>
        <select name="region" ref="region" v-if="this.plist.length!==0"><option :value="item.id" v-for="(item,index) in rlist" :key="index">{{ item.name }}</option></select> 
      </div>
      <div><label for="">详细地址:  </label><textarea name="" id="" ref="detail"></textarea></div>
    </form>
    <div class="footer">
      <div class="btn-buy" @click="addAddress">保存</div>
    </div>
  </div>
</template>

<script>
import {addAddress,getProvince} from '@/api/address.js'
export default {
  data(){
    return {
      name:'',
      phone:'',
      plist:[],
      activeIndex1:0,
      activeIndex2:0,
    }
  },
  computed:{
    clist(){
      return this.plist.length!==0?Object.values(this.plist[this.activeIndex1].city):[]
    },
    rlist(){
      return this.clist.length!==0?Object.values(this.clist[this.activeIndex2].region):[]
    }
  },
  async created(){
    const res = await getProvince()
    console.log(res)
    this.plist = Object.values(res.data.list)
  },
  methods:{
    onClickLeft(){
      this.$router.back()
    },
    async addAddress(){
      const res = await addAddress(this.name,this.phone,this.$refs.province.value,this.$refs.city.value,this.$refs.region.value,this.$refs.detail.value)
      console.log(res)
      this.$toast.success("添加地址成功")
      setTimeout(()=>{
        this.$router.replace('/address')
      },1000)
    },
    changeIndex1(e){
      this.plist.forEach((item,index)=>{
        if(item.id==e.target.value){
          this.activeIndex1 = index
        }
      })
    },
    changeIndex2(e){
      this.clist.forEach((item,index)=>{
        if(item.id==e.target.value){
          this.activeIndex2 = index
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
.body{
  form{
    padding: 20px;
    div{
      line-height: 30px;
      margin-bottom: 10px;
      input{
        padding: 5px;
      }
      select {
        height: 40px; /* 你可以根据需要设置一个合适的高度 */
        overflow-y: auto; /* 启用垂直滚动条 */
      }
      
    }
    div:first-child{
      font-size: 20px;
    }
    div:last-child{
      display: flex;
      textarea {
        margin-left: 6px;
        padding-left: 10px;
        flex: 1;
        vertical-align: middle;
      }
    }
  }
}
.footer {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 55px;
    background-color: #fff;
    border-top: 1px solid #ccc;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    .btn-buy {
      margin: 0 10px;
      height: 36px;
      line-height: 36px;
      flex:1;
      border-radius: 18px;
      text-align: center;
      color: #fff;
      font-size: 14px;
      background-color: #fe5630;
    }
}
</style>